<template>
  <div class="userEditorFields--wrapper">
    <form @submit.prevent="submit" novalidate class="userEditorFields--form">
      <InputWithLabel
        v-if="textLayers.header"
        v-model="textLayers.header.text"
        label="Header"
        placeholder="Header"
        type="text"
        max="54"
      />
      <InputWithLabel
        v-if="textLayers.description"
        v-model="textLayers.description.text"
        label="Description"
        placeholder="Description"
        type="text"
        max="54"
      />
      <InputWithLabel
        v-if="textLayers.buttonName"
        v-model="textLayers.buttonName.text"
        label="Button name"
        placeholder="Button name"
        type="text"
        max="54"
      />
      <WithLabel label="Brand color">
        <ColorsList :colorList="colorList" v-model="formData.color" />
      </WithLabel>
    </form>
  </div>
</template>

<script>
import InputWithLabel from '@/components/inputs/InputWithLabel/InputWithLabel.vue';
import WithLabel from '@/components/WithLabel/WithLabel.vue';
import ColorsList from '@/views/BannerGenerator/GeneratorMenu/ColorsList/ColorsList.vue';
import { useBannersStore } from '@/stores/useBannersStore';

const _colorList = ['#FFFFFF', '#2467EC', '#F8AC67', '#25262C'];

export default {
  setup() {
    return {
      bannersStore: useBannersStore(),
    };
  },
  data() {
    return {
      formData: {
        color: _colorList[0],
      },
      colorList: _colorList,
    };
  },
  computed: {
    textLayers() {
      return this.bannersStore.getLayersByTitles([
        'header',
        'description',
        'buttonName',
      ]);
    },
  },
  components: { InputWithLabel, WithLabel, ColorsList },
};
</script>

<style lang="scss" scoped>
.userEditorFields {
  &--wrapper {
    background: var(--color-white);
    border-radius: 25px;
    padding: 21px 32px;
    height: max-content;
  }

  &--form {
    display: grid;
    row-gap: 25px;
  }
}
</style>
