<template>
  <div class="userEditor--container">
    <UserEditorFields />
    <div>
      <UserEditorHeader
        :conversion="conversion"
        :downloadsCount="downloadsCount"
      />
      <UserEditorCanvas />
    </div>
    <UserEditorSizes />
  </div>
</template>

<script>
import UserEditorFields from './UserEditorFields/UserEditorFields.vue';
import UserEditorHeader from './UserEditorHeader/UserEditorHeader.vue';
import UserEditorCanvas from './UserEditorCanvas/UserEditorCanvas.vue';
import UserEditorSizes from './UserEditorSizes/UserEditorSizes.vue';
import { useBannersStore } from '@/stores/useBannersStore';

export default {
  setup() {
    return {
      bannersStore: useBannersStore(),
    };
  },
  mounted() {
    this.init();
  },
  computed: {
    conversion() {
      return this.bannersStore.conversion;
    },
    downloadsCount() {
      return this.bannersStore.downloadsCount;
    },
  },
  methods: {
    async init() {
      const id = this.$route.params?.id;

      await this.bannersStore.getBanner(id);
    },
  },
  components: {
    UserEditorFields,
    UserEditorHeader,
    UserEditorCanvas,
    UserEditorSizes,
  },
};
</script>

<style lang="scss" scoped>
.userEditor {
  &--container {
    display: grid;
    grid-template-columns: 290px 500px 290px;
    column-gap: 60px;
  }
}
</style>
