<template>
  <div class="similar_banners">
    <div class="title">Similar banners:</div>
    <ul v-if="banners.length" class="banners_list">
      <BannerCard
        isSimilar
        v-for="banner of banners"
        :key="banner.id"
        :id="banner.id"
        :conversion="banner.conversion"
        :downloadsCount="banner.downloadsCount"
        :imgUrl="banner.imgUrl"
      />
    </ul>
    <div v-else class="banners_empty">
      <img src="../../assets/images/emoji-sad-cat.png" alt="" />
      <h3>Нет похожих баннеров</h3>
    </div>
  </div>
</template>

<script>
import { useAppStore } from '@/stores/useAppStore';
import { useGeneratedBannersStore } from '@/stores/useGeneratedBannersStore';
import { useToaster } from '@/utils/useToaster';
import BannerCard from '../BannerCard/BannerCard.vue';

export default {
  data: () => ({
    banners: [],
  }),
  setup() {
    return {
      appStore: useAppStore(),
      toaster: useToaster(),
      generatedBannersStore: useGeneratedBannersStore(),
    };
  },
  mounted() {
    this.init();
  },
  methods: {
    async getGeneratedBanners() {
      try {
        await this.generatedBannersStore.getGeneratedBanners();
      } catch (error) {
        console.error(error);
        this.toaster.error('Не получилось загрузить баннеры :(');
      }
    },
    async init() {
      this.appStore.toggleAppLoader(true);

      await this.getGeneratedBanners();

      this.banners = this.generatedBannersStore.banners;

      this.banners.length = 4;
      setTimeout(() => {
        this.appStore.toggleAppLoader(false);
      }, 135);
    },
  },
  components: { BannerCard },
};
</script>

