<template>
  <div class="userEditor--canvas-wrapper" ref="userEditorContainer">
    <BannerCanvas
      v-if="bannerStatus === 'success'"
      :canvas="canvasItem"
      :scheme="banner?.schemes[0]?.id"
      :banner="banner"
      :zoom="canvasWidth / canvasItem.width"
      rounded
    />
  </div>
</template>

<script>
import BannerCanvas from '@/components/BannerCanvas.vue';
import { useBannersStore } from '@/stores/useBannersStore';

export default {
  setup() {
    return {
      bannersStore: useBannersStore(),
    };
  },
  data() {
    return {
      canvasWidth: 0,
      canvasResizeObserver: null,
    };
  },
  computed: {
    banner() {
      return this.bannersStore.banner.data;
    },
    bannerStatus() {
      return this.bannersStore.banner.status;
    },
    canvasItem() {
      return this.bannersStore.canvases[0];
    },
  },
  mounted() {
    this.init();
    this.calculateCanvasWidth();
  },
  methods: {
    init() {
      this.canvasResizeObserver = new ResizeObserver((entries) => {
        for (let entry of entries) {
          this.canvasWidth = entry.contentRect.width;
        }
      });

      this.canvasResizeObserver.observe(this.$refs.userEditorContainer);
    },
    calculateCanvasWidth() {
      this.canvasWidth = this.$refs.userEditorContainer.offsetWidth;
    },
  },
  beforeUnmount() {
    this.canvasResizeObserver.disconnect();
  },
  components: { BannerCanvas },
};
</script>

<style lang="scss" scoped>
.userEditor {
  &--canvas {
    &-wrapper {
      display: flex;
      justify-content: center;
      padding: 9px;
      width: 500px;
      background-color: var(--color-white);
      border-radius: 15px;
    }
  }
}
</style>
