<template>
  <div>
    <h3 class="userEditor--sizesLabel">Под какие размеры нужен баннер?</h3>
    <div class="userEditor--sizesList">
      <div
        class="userEditor--sizesItem"
        v-for="({ checked, title, height, width }, index) in sizes"
        :key="index"
      >
        <AppCheckbox
          :hideValue="true"
          :modelValue="checked"
          @update:modelValue="(value) => checkSize(index, value)"
        />
        <div class="userEditor--sizesItem-image-wrapper">
          <div
            class="userEditor--sizesItem-image"
            :style="{
              '--sizeItem-image-width': getWidthByRatio(width / height),
              '--sizeItem-image-height': getHeightByRatio(width / height),
            }"
          >
            <AppIcon icon="gallery-bold" :size="20" />
          </div>
        </div>
        <div class="userEditor--sizesItem-info">
          <div class="label">подходит для</div>
          <h4 class="userEditor--sizesItem-name">{{ title }}</h4>
          <div class="userEditor--sizesItem-size label">
            {{ `${width}x${height}` }}
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import AppCheckbox from '@/components/AppCheckbox.vue';
import { useBannersStore } from '@/stores/useBannersStore';

export default {
  setup() {
    return {
      bannersStore: useBannersStore(),
    };
  },
  methods: {
    checkSize(id, value) {
      this.sizes[id].checked = value;
    },
    getWidthByRatio(ratio) {
      return ratio < 1 ? '36px' : '64px';
    },
    getHeightByRatio(ratio) {
      return ratio > 1 ? '36px' : '64px';
    },
  },
  computed: {
    sizes() {
      return this.bannersStore.canvases;
    },
  },
  components: { AppCheckbox },
};
</script>

